@import url(partials/reset.css);

@import url(partials/fonts.css);

/* my style */

a {
  text-decoration: none;
}

*:focus {
  outline: none;
}

html {
  width: 100%;
  height: 100%;
}

body {
  font-family: "Squad", sans-serif;
  margin: 0;
  font-size: 1vw;
  color: #113A03;
  background: none transparent;
}

.game {
  width: 100%;
  height: 57.91667vw;
  box-sizing: border-box;
  padding: 1.5625vw 4.53125vw 0;
  overflow: hidden;
}

.game__title {
  font-weight: 800;
  font-size: 3.33333vw;
  line-height: 0.87;
  text-align: center;
  color: #76BD5E;
  position: relative;
  z-index: 2;
}

.game__flex {
  display: flex;
  justify-content: space-between;
  margin-top: 4.01042vw;
  padding-left: 5.83333vw;
}

.game__left {
  position: relative;
  z-index: 1;
  width: 30.41667vw;
  padding-top: 1.09375vw;
}

.game__left::before {
  content: "";
  display: block;
  z-index: -1;
  background: url("../images/paper.png") center center no-repeat;
  background-size: contain;
  user-select: none;
  width: 42.5vw;
  height: 52.86458vw;
  position: absolute;
  left: -3.48958vw;
  top: -4.94792vw;
  pointer-events: none;
}

.game__right {
  position: relative;
  padding-right: 4.53125vw;
  z-index: 3;
}

.game__dropzone {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 27.34375vw;
}

.game__dropitem {
  width: calc(100% / 3);
  padding: 0 0.96354vw 1.04167vw;
  box-sizing: border-box;
}

.game__hole {
  width: 7.1875vw;
  height: 7.1875vw;
  background: url("../images/hole.png") center center no-repeat;
  background-size: contain;
  flex-shrink: 0;
  border-radius: 100%;
}

.game__hole img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.game__hole img.dragged {
  opacity: 0.1;
  user-select: none;
  pointer-events: none;
}

.game__hole img:not(.dragged) {
  cursor: grab;
}

.game__hole img:not(.dragged):active {
  cursor: grabbing;
}

.game__dragzone {
  display: flex;
  flex-wrap: wrap;
  width: 40.72917vw;
  overflow: hidden;
  height: 39.375vw;
}

.game__dragzone[data-scroll="0"] .game__item {
  transform: translateY(0);
}

.game__dragzone[data-scroll="1"] .game__item {
  transform: translateY(-13.125vw);
}

.game__dragzone[data-scroll="2"] .game__item {
  transform: translateY(-26.25vw);
}

.game__dragzone[data-scroll="3"] .game__item {
  transform: translateY(-39.375vw);
}

.game__item {
  width: calc(100% / 5);
  padding: 0 0.44271vw 0.98958vw;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 13.125vw;
  transition: transform 0.5s;
}

.game__name {
  font-size: 1.25vw;
  line-height: 1.25vw;
  text-align: center;
  color: #113A03;
  margin-top: 0.98958vw;
  height: 3.95833vw;
}

.game__name--m {
  font-size: 1.09375vw;
}

.game__name--sm {
  font-size: 0.9375vw;
}

.game__name--s {
  font-size: 0.83333vw;
  line-height: 0.9375vw;
}

.game .game__arrow {
  position: absolute;
  display: block;
  width: 2.34375vw;
  height: 2.34375vw;
  right: 0;
}

.game .game__arrow--up {
  top: 0.625vw;
}

.game .game__arrow--down {
  bottom: 2.23958vw;
}

.game .game__arrow svg {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}

.game .game__arrow svg path {
  fill: #113a03;
}

.game .game__arrow--disabled {
  cursor: default;
}

.game .game__arrow--disabled svg path {
  fill: #E5E5E5;
}

.game .game__arrow:not(.game__arrow--disabled):hover svg path {
  fill: #76BD5E;
}

.game__btns {
  display: flex;
  justify-content: center;
  margin-top: 1.30208vw;
}

.game__btn {
  display: block;
  background: #76BD5E;
  border-radius: 0.625vw;
  border: none;
  cursor: pointer;
  width: 14.16667vw;
  height: 3.95833vw;
  font-weight: bold;
  font-size: 1.25vw;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  transition: 0.2s;
  margin: 0.52083vw;
}

@media (min-width: 1000px) {
  .game__btn:hover {
    background: #4b793c;
  }
}

@media (max-width: 999px) {
  .game {
    padding: 0;
    height: 241.54589vw;
  }

  .game__title {
    font-size: 11.5942vw;
    line-height: 1;
  }

  .game__flex {
    justify-content: normal;
    margin-top: 6.03865vw;
    padding-left: 0;
    flex-direction: column-reverse;
  }

  .game__left {
    width: 100%;
    padding-top: 0;
    margin-top: 4.83092vw;
  }

  .game__left::before {
    width: 141.78744vw;
    height: 176.57005vw;
    position: absolute;
    left: -10.14493vw;
    top: -21.98068vw;
  }

  .game__right {
    padding-right: 0;
  }

  .game__dropzone {
    width: 91.30435vw;
  }

  .game__dropitem {
    padding: 0 3.1401vw 3.38164vw;
  }

  .game__hole {
    width: 23.91304vw;
    height: 23.91304vw;
    background-image: url("../images/hole-mob.png");
  }

  .game__dragzone {
    width: auto;
    flex-wrap: nowrap;
    height: auto;
    overflow-x: auto;
    padding: 0 6.28019vw;
  }

  .game__dragzone .game__item {
    transform: none !important;
  }

  .game__item {
    width: 27.05314vw;
    padding: 0 1.44928vw;
    height: auto;
  }

  .game__name {
    font-size: 4.34783vw;
    line-height: 5.07246vw;
    height: auto;
    margin-top: 3.1401vw;
  }

  .game__name--m {
    font-size: 3.86473vw;
  }

  .game__name--sm {
    font-size: 3.38164vw;
  }

  .game__name--s {
    font-size: 3.1401vw;
  }

  .game .game__arrow {
    display: none;
  }

  .game__btns {
    flex-wrap: wrap;
    margin-top: 2.41546vw;
  }

  .game__btn {
    border-radius: 2.89855vw;
    width: 65.70048vw;
    height: 18.35749vw;
    font-size: 6.76329vw;
    margin: 2.41546vw;
  }
}